/*
 * CSS Variables and Root Declarations
 * Global variables used throughout the theme
 */

:root {
     /* Colors */
     --accent-color: oklch(0.58 0.198875 255.9725); /*#0077ed*/
     --gray-dark: oklch(0.23 0.0038 286.1); /*#1d1d1f*/
     --gray-light: oklch(0.98 0.0026 286.35); /*#f9f9fb*/
     --gray-medium: oklch(0.44 0.0118 252.92); /*#4D5258*/
     --primary-color: oklch(0 0 0); /*#000000*/
     --secondary-color: oklch(1 0 0); /*#ffffff*/

     --text-on-yellow: #403e38;
     --heading-brown: #403e38;
     --page-text: #2a2200;
     --highlight-yellow: oklch(0.93 0.1181 96.14); /*#ffe889*/
     --excerpt-color: #554400;
     --meta-text: #8f8f8b;
     --background-cream: #FFFDF3;
     --background-yellow-light: #fff9df;
     --background-off-white: #fcfcfc;
     --border-light: #f0f0f0;
     --tag-description: #CAC8BF;
     --status-success-bg: #E3F8DF;
     --status-error-bg: #FFEBEB;
     --gold-accent-10: rgba(193, 154, 107, 0.1);
     --gold-accent-30: rgba(193, 154, 107, 0.3);

     --black: #000;
     --blue: #007AFF;
     --light-blue: rgb(250, 252, 255);
     --green: #34C759;
     --light-green: rgb(251, 254, 252);
     --indigo: #5856D6;
     --orange: #FF9500;
     --pink: #FF2D55;
     --purple: #AF52DE;
     --red: oklch(65% 0.2321 28.66); /*#ff3B30*/
     --light-red: rgb(255, 247, 247);
     --teal: #5AC8FA;
     --yellow: #FFCC00;

     /* Typography */
     --display: 'neue-haas-grotesk-display', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
     --sans: 'neue-haas-grotesk-text', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
     --serif: portada, serif;
     --mono: 'JetBrains Mono', 'calling-code', 'SF Mono', ui-monospace, Menlo, Consolas, Roboto Mono, 'Ubuntu Mono', 'DejaVu Sans Mono', monospace;

     /* Font sizes - core sizes with rem units */
     --text-2xl: 1.5rem;
     /* 24px */
     --text-base: 1rem;
     /* 16px */
     --text-baseplus: 1.1rem;
     /* 17px */
     --text-lg: 1.125rem;
     /* 18px */
     --text-sm: 0.875rem;
     /* 14px */
     --text-xl: 1.25rem;
     /* 20px */
     --text-xs: 0.75rem;
     /* 12px */
     --text-xxs: 0.6875rem;
     /* 11px */

     /* Pixel-to-REM Conversions */
     --size-1: 0.0625rem;
     /* 1px */
     --size-2: 0.125rem;
     /* 2px */
     --size-3: 0.1875rem;
     /* 3px */
     --size-4: 0.25rem;
     /* 4px */
     --size-5: 0.3125rem;
     /* 5px */
     --size-6: 0.375rem;
     /* 6px */
     --size-7: 0.4375rem;
     /* 7px */
     --size-8: 0.5rem;
     /* 8px */
     --size-9: 0.5625rem;
     /* 9px */
     --size-10: 0.625rem;
     /* 10px */
     --size-11: 0.6875rem;
     /* 11px */
     --size-12: 0.75rem;
     /* 12px */
     --neg-size-2: calc(var(--size-2) * -1);
     /* -2px */
     --neg-size-3: calc(var(--size-3) * -1);
     /* -3px */

     /* Heading sizes with subtle fluid scaling */
     --heading-lg: clamp(1.75rem, 1.6rem + 0.75vw, 2rem);
     /* ~28-32px */
     --heading-md: clamp(1.5rem, 1.4rem + 0.5vw, 1.75rem);
     /* ~24-28px */
     --heading-sm: clamp(1.375rem, 1.3rem + 0.3vw, 1.5rem);
     /* ~22-24px */
     --heading-xl: clamp(2rem, 1.8rem + 1vw, 2.5rem);
     /* ~32-40px */

     /* Line heights - unitless for better scaling */
     --line-height-normal: 1.5;
     --line-height-relaxed: 1.7;
     --line-height-tight: 1.2;

     /* Font weights */
     --font-weight-black: 800;
     --font-weight-bold: 700;
     --font-weight-heavy: 900;
     --font-weight-light: 300;
     --font-weight-medium: 500;
     --font-weight-normal: 400;
     --font-weight-semibold: 600;

     /* Letter spacing */
     --letter-spacing-normal: 0;
     --letter-spacing-tight: -0.03rem;
     --letter-spacing-tightest: -0.03rem;
     --letter-spacing-wide: 0.05em;

     /* Spacing scale */
     --space-2xs: 0.25rem;
     /* 4px */
     --space-xs: 0.5rem;
     /* 8px */
     --space-sm: 0.75rem;
     /* 12px */
     --space-md: 1rem;
     /* 16px */
     --space-lg: 1.25rem;
     /* 20px */
     --space-xl: 1.5rem;
     /* 24px */
     --space-2xl: 2rem;
     /* 32px */
     --space-3xl: 3rem;
     /* 48px */

     /* Container widths */
     --content-lg: 56.25rem;
     /* 900px - your current max-width */
     --content-md: 45rem;
     /* 720px */
     --content-sm: 30rem;
     /* 480px */
     --content-xl: 75rem;
     /* 1200px */
     --content-xs: 20rem;
     /* 320px */

     /* UI Elements */
     --radius: 0.5rem;
     --shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1), 0 0 0 1px rgb(193 154 107 / 0.1);
     --transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);

     /* Toggle appearance */
     --toggle-active-dark: #3f3f3f;
     --toggle-active-light: #fffcef;
     --toggle-bg-dark: #1e2022;
     --toggle-bg-light: #f7f7f9;
     --toggle-icon-active-dark: #ffffff;
     --toggle-icon-active-light: #000000;
     --toggle-icon-dark: #a1a1aa;
     --toggle-icon-light: #666666;
     --toggle-shadow-dark: 0 var(--size-1) var(--size-2) rgba(255, 255, 255, 0.1);
     --toggle-shadow-light: 0 var(--size-1) var(--size-2) rgba(0, 0, 0, 0.1);
 }
